'use client'

import { useState } from 'react'
import { useRouter } from 'next/navigation'
import { Globe, Clock, Tag, Loader2 } from 'lucide-react'

import { Button } from '@/src/shared/ui/button'
import { Input } from '@/src/shared/ui/input'
import { Label } from '@/src/shared/ui/label'
import {
	Card,
	CardContent,
	CardHeader,
	CardTitle,
	CardDescription,
} from '@/src/shared/ui/card'

import { cn } from '@/lib/utils'

export const CreateMonitorForm = () => {
	const router = useRouter()
	const [isLoading, setIsLoading] = useState(false)
	const [formData, setFormData] = useState({
		name: '',
		url: '',
		interval: 60,
	})

	const handleSubmit = async (e: React.FormEvent) => {
		e.preventDefault()
		setIsLoading(true)

		try {
			const res = await fetch(`${process.env.NEXT_PUBLIC_API_URL}/monitors`, {
				method: 'POST',
				body: JSON.stringify(formData),
				headers: { 'Content-Type': 'application/json' },
			})

			if (res.ok) {
				router.push('/')
				router.refresh()
			}
		} catch (err) {
			console.error('Fetch error:', err)
		} finally {
			setIsLoading(false)
		}
	}

	return (
		<form onSubmit={handleSubmit} className='space-y-6'>
			{/* Основные данные */}
			<Card className='border-slate-200 shadow-sm dark:border-slate-800 dark:bg-slate-900'>
				<CardHeader>
					<CardTitle className='flex items-center gap-2 text-lg text-slate-900 dark:text-slate-100'>
						<Tag className='w-5 h-5 text-blue-600 dark:text-blue-500' />
						Основные данные
					</CardTitle>
					<CardDescription className='text-slate-500 dark:text-slate-400'>
						Введите название и адрес ресурса
					</CardDescription>
				</CardHeader>

				<CardContent className='space-y-4'>
					<div className='space-y-2'>
						<Label
							htmlFor='name'
							className='text-slate-700 dark:text-slate-300'
						>
							Название
						</Label>
						<Input
							id='name'
							placeholder='Напр: API Продакшн'
							value={formData.name}
							onChange={(e) =>
								setFormData({ ...formData, name: e.target.value })
							}
							required
							className='bg-white dark:bg-slate-950 dark:border-slate-800 dark:text-slate-100 dark:placeholder:text-slate-500'
						/>
					</div>

					<div className='space-y-2'>
						<Label htmlFor='url' className='text-slate-700 dark:text-slate-300'>
							URL адрес
						</Label>
						<div className='relative'>
							<Globe className='absolute left-3 top-3 h-4 w-4 text-slate-400 dark:text-slate-500' />
							<Input
								id='url'
								type='url'
								className='pl-9 bg-white dark:bg-slate-950 dark:border-slate-800 dark:text-slate-100 dark:placeholder:text-slate-500'
								placeholder='https://example.com'
								value={formData.url}
								onChange={(e) =>
									setFormData({ ...formData, url: e.target.value })
								}
								required
							/>
						</div>
					</div>
				</CardContent>
			</Card>

			{/* Частота проверок */}
			<Card className='border-slate-200 shadow-sm dark:border-slate-800 dark:bg-slate-900'>
				<CardHeader>
					<CardTitle className='flex items-center gap-2 text-lg text-slate-900 dark:text-slate-100'>
						<Clock className='w-5 h-5 text-blue-600 dark:text-blue-500' />
						Частота проверок
					</CardTitle>
					<CardDescription className='text-slate-500 dark:text-slate-400'>
						Как часто мы должны проверять доступность?
					</CardDescription>
				</CardHeader>

				<CardContent>
					<div className='grid grid-cols-2 sm:grid-cols-4 gap-3'>
						{[60, 300, 600, 3600].map((sec) => (
							<Button
								key={sec}
								type='button'
								variant={formData.interval === sec ? 'default' : 'outline'}
								className={cn(
									'h-12 text-xs font-semibold uppercase tracking-wider',
									formData.interval === sec
										? 'bg-blue-600 hover:bg-blue-700 dark:bg-blue-500 dark:hover:bg-blue-600'
										: 'dark:border-slate-700 dark:text-slate-300 dark:hover:bg-slate-800'
								)}
								onClick={() => setFormData({ ...formData, interval: sec })}
							>
								{sec < 3600 ? `${sec / 60} мин.` : '1 час'}
							</Button>
						))}
					</div>
				</CardContent>
			</Card>

			{/* Submit */}
			<Button
				type='submit'
				disabled={isLoading}
				className='w-full h-12 text-base font-semibold bg-blue-600 hover:bg-blue-700 shadow-md dark:bg-blue-500 dark:hover:bg-blue-600'
			>
				{isLoading ? (
					<>
						<Loader2 className='mr-2 h-4 w-4 animate-spin' />
						Создание...
					</>
				) : (
					'Запустить мониторинг'
				)}
			</Button>
		</form>
	)
}
